    color:#fff !important;
    background-color: #FFCF64 !important;
    user-select: none;
}


.btn-sucess {
    width: 167px !important;
    height: 109px !important;
    border-radius: 100px !important;
}

.btn-continue {
    float: right !important;
    position: relative;
    top:55px;
