     user-select: none;
}



.bloco-ausente h1{
   position: relative;
   left:38px;
   top:-18px;
}

.btn-back-checklist {
    position: relative;
    left:10px;
    top:8%;
    float: left;
}

.header-card-paciente {
    color: #fff !important;
    background-color: #37BBAA;
}

.bloco-info-paciente {
    width: auto;
    height:auto;
    position: relative;
    text-align: center;
    display:flex;
    font-size: 22px;
}

.bloco-info-paciente div {
    padding-left:15px;
    padding-right:15px;
}

.header-nome-paciente {
    font-size: 32px;
}

.header-card-checklist {
    color: #fff;
    background-color: #4D4F53 !important;
}

.header-checklist {
    font-size: 30px;
}

tr > td {
    font-size: large;
}

tr > th{
    font-size: large;
}

.checks{   

    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;

    border-radius: 80%;
    width: 30px;
    height: 30px;

    border: 2px solid #999;
    transition: 0.2s all linear;
    margin-right: 5px;

    position: relative;
    top: 4px;
}

.checks:checked{
    background: #6983F5;
}

.input-time{
    
    width: 140px;
    height: 50px;   
    font-size: 25px;
    color:#fff;
    border:none;
    text-align: center;
    padding-left:30px;
    background: #6983F5;
    border-radius: 30px;
}


.input-datatime{
    position: absolute;
    margin-top:-25px;
    margin-left:-250px;
    width: 300px;
    height: 50px;   
    font-size: 25px;
    color:#fff;
    border:none;
    text-align: center;
    padding-left:30px;
    background: #6983F5;
    border-radius: 30px;
}

input[type="time"]::-webkit-calendar-picker-indicator {
    background: none;
}


.radio-preparo {
    -webkit-appearance: none;
    width: 55px;
    height: 55px;
    border-radius: 50%;
    outline: none;
}

.radio-preparo:before  {
    content: '';
    display: block;
    width: 60%;
    height: 60%;
    margin: 20% auto;
    border-radius: 50%;
}

.radio-preparo:checked:before {
    background: rgb(60, 60, 60);
}

